import ChimeeMobilePlayer from "chimee-mobile-player";
import Header from "@/components/common/Header";
import VideoPlayer from "@/components/common/VideoPlayer";
import ScenceStoryDetailDesc from "@/components/layout/ScenceStoryDetailDesc";
import DividedArea from "@/components/common/DividedArea";
import HorizontalScroller from "@/components/common/HorizontalScroller";
import CommentList from "@/components/layout/CommentListPlayRL";
import Comments from "@/components/common/Comments";
import { ScenceRememberAndLearnDetails } from "@/servers/api";

export default {
  name: "",
  props: [""],
  data() {
    return {
      TitleObjData: {
        titleContent: "",
        showLeftBack: true,
        showRightMore: false
      },
      videoData: {},
      currentId: "",
      pullDownRefreshObj: {
        threshold: 30,
        stop: 30
      },
      commitDataList: [],
      imgListObj: {
        ImgList: [],
        index: 0,
        dotsPosition: "center",
        loop: true,
        auto: true,
        height: "220px"
      },
      goodsDataList: []
    };
  },

  components: {
    Header,
    VideoPlayer,
    ScenceStoryDetailDesc,
    DividedArea,
    HorizontalScroller,
    CommentList,
    Comments
  },

  computed: {
    contentHeight() {
      return { height: document.documentElement.clientHeight - 45 + "px" };
    }
  },

  beforeMount() {},

  mounted() {
    this.getData();
  },

  methods: {
    //更新评论数据列表
    updateCommintList() {
      let arr = [];
      ScenceRememberAndLearnDetails({
        id: this.$route.query.id,
        type: this.$route.query.branch,
        page: 1
      })
        .then(res => {
          console.log(res);
          if (res.result === 1) {
            if (res.data.comment.length) {
              arr = JSON.parse(JSON.stringify(res.data.comment));
            }
          }
          this.commitDataList = arr;
        })
        .catch(err => {
          console.log(err);
        });
    },
    // 初始化数据
    getData() {
      this.currentId = this.$route.query.id;
      ScenceRememberAndLearnDetails({
        id: this.$route.query.id,
        type: this.$route.query.branch,
        page: 1
      })
        .then(res => {
          console.log(res);
          let arr = [];
          if (res.result === 1) {
            if (res.data.video) {
              this.createVideoDom(true, res.data.video);
              this.clickState = res.data.video.type;
              this.praiseNum = res.data.video.praise_num;
              this.videoData = res.data.video;
              this.goodsDataList = res.data.goods;
            }
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    createVideoDom(flag, videoObj) {
      new ChimeeMobilePlayer({
        wrapper: "#wrapper",
        src: videoObj.video_url,
        autoplay: false,
        poster: videoObj.video_img,
        controls: flag,
        playsInline: true,
        preload: "auto",
        x5VideoPlayerFullscreen: true,
        x5VideoOrientation: "landscape|portrait",
        xWebkitAirplay: true,
        muted: true
      });
    }
  },

  watch: {}
};



